.skeleton-loader__bone {
  height: 18px;
  position: relative;
  background: var(--swap-loading-background-color, var(--swap-default-loading-background-color));
  border-radius: 5px;
  overflow: hidden;
}
.skeleton-loader__bone:after {
  background: linear-gradient(90deg, hsla(0, 0%, 100%, 0), hsla(0, 0%, 100%, 0.1), hsla(0, 0%, 100%, 0));
  animation: loading 1.5s infinite;
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transform: translateX(-100%);
  z-index: 1;
}
.dark .skeleton-loader__bone:after {
  background: linear-gradient(90deg, hsla(0, 0%, 100%, 0), hsla(0, 0%, 100%, 0.1), hsla(0, 0%, 100%, 0));
}

.skeleton-loader .content-hidden {
  visibility: hidden;
}
